body {
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  color: #12567a;
  font-size: 12px;
  line-height: 20px;
  width: 100%;
}

h1, h2, h3, h4, h5, h6 {
  color: #444;
}

/* default font size */
.fa {
  font-size: 14px;
}

/* Override the bootstrap defaults */
h1 {
  font-size: 33px;
}

h2 {
  font-size: 27px;
}

h3 {
  font-size: 21px;
}

h4 {
  font-size: 15px;
}

h5 {
  font-size: 12px;
}

h6 {
  font-size: 10.2px;
}

a {
  color: #4979cf;
}

a:hover {
  text-decoration: none;
}

legend {
  font-size: 18px;
  padding: 7px 0px;
}

label {
  font-size: 12px;
  font-weight: normal;
}

select.form-control, textarea.form-control, input[type=text].form-control, input[type=password].form-control, input[type=datetime].form-control, input[type=datetime-local].form-control, input[type=date].form-control, input[type=month].form-control, input[type=time].form-control, input[type=week].form-control, input[type=number].form-control, input[type=email].form-control, input[type=url].form-control, input[type=search].form-control, input[type=tel].form-control, input[type=color].form-control {
  font-size: 12px;
}

.input-group input, .input-group select, .input-group .dropdown-menu, .input-group .popover {
  font-size: 12px;
}

.input-group .input-group-addon {
  font-size: 12px;
  height: 30px;
  background-color: #f2f8fd;
  border: 1px solid #e4f1fc;
}

.form-control {
  border: 1px solid #e4f1fc;
}

/* Fix some bootstrap issues */
span.hidden-xs, span.hidden-sm, span.hidden-md, span.hidden-lg {
  display: inline;
}

a:active, a:focus {
  outline: none;
}

textarea {
  resize: none;
}

textarea {
  resize: vertical;
}

textarea {
  resize: horizontal;
}

.btn:active, .btn:focus {
  outline: thin dotted #333;
  outline: 5px auto -webkit-focus-ring-color;
  outline-offset: -2px;
  outline: none;
}

.btn::-moz-focus-inner {
  border: 0;
}

button:active, button:focus {
  outline: none !important;
}

button::-moz-focus-inner {
  border: 0 !important;
}

:focus {
  outline: none;
}

:focus {
  -moz-outline: 3px solid #fff !important;
}

input, textarea {
  outline: none;
}

input:active, textarea:active {
  outline: none;
}

input[type=radio]:focus {
  -moz-outline-radius: 12px !important;
  -moz-outline-offset: 0px !important;
}

button[type=radio]:focus {
  -moz-outline-radius: 12px !important;
  -moz-outline-offset: 0px !important;
}

button::-moz-focus-inner,
input[type=reset]::-moz-focus-inner,
input[type=button]::-moz-focus-inner,
input[type=submit]::-moz-focus-inner,
input[type=submit]::-moz-focus-inner,
input[type=file] > input[type=button]::-moz-focus-inner {
  border: none;
}

input[type=checkbox], input[type=radio], .checkbox input[type=checkbox], .checkbox-inline input[type=checkbox], .radio input[type=radio], .radio-inline input[type=radio] {
  cursor: pointer;
}

.nav-tabs {
  margin-bottom: 15px;
  border-bottom: 1px solid #e4f1fc;
}

.nav-tabs > li.active > a, .nav-tabs > li.active > a:focus, .nav-tabs > li.active > a:hover {
  border: 1px solid #e4f1fc;
  border-bottom-color: transparent;
}

.nav > li > a:focus, .nav > li > a:hover {
  background-color: #f2f8fd;
}

.nav-tabs > li > a:hover {
  border-color: #e4f1fc;
}

div.required .control-label:before {
  content: "* ";
  color: #F00;
  font-weight: bold;
}

/* Gradent to all drop down menus */
.dropdown-menu li > a:hover {
  text-decoration: none;
  color: #ffffff;
  background-color: #376aca;
  background-image: linear-gradient(to bottom, #4979cf, #2f64ca);
  background-repeat: repeat-x;
}

/* top */
#top {
  border-bottom: 1px solid #e4f1fc;
  padding: 4px 0px 3px 0;
  margin: 0 0 20px 0;
  min-height: 40px;
}

#top .container {
  padding: 0 20px;
}

#top #form-currency .currency-select,
#top #form-language .language-select {
  font-size: 12px;
  padding: 3px 20px;
  text-align: left;
}

#top #form-currency .currency-select:hover,
#top #form-language .language-select:hover {
  text-shadow: none;
  color: #ffffff;
  background-color: #376aca;
  background-image: linear-gradient(to bottom, #4979cf, #2f64ca);
  background-repeat: repeat-x;
}

#form-currency .btn-link, #form-language .btn-link {
  padding: 0 0 0 7.5px;
}

#top .btn-link, #top-links li, #top-links a, #head-links li, #head-links a {
  background-color: transparent;
  color: #888;
  font-size: 12px;
  text-shadow: 0 1px 0 #FFF;
  text-decoration: none;
}

#top .btn-link:hover, #top-links a:hover, #head-links .btn-link:hover, #head-links a:hover {
  color: #444;
}

#top-links .dropdown-menu a {
  text-shadow: none;
}

#top-links .dropdown-menu a:hover {
  color: #FFF;
}

#top .btn-link strong {
  font-size: 14px;
  line-height: 14px;
}

#top-links, #head-links {
  padding-top: 6px;
}

#top-links a + a, #head-links a + a {
  margin-left: 15px;
}

#top-links hr {
  margin: 5px 15px;
}

/* logo -fix */
#logo {
  margin: 0 0 10px 0;
}

#logo .img-responsive {
  width: 200px;
  padding-bottom: 10px;
}

@media (max-width: 767px) {
  #logo {
    text-align: center;
  }
  #logo img {
    display: inline-block;
  }
  #contact {
    text-align: center;
    font-size: 25px;
    padding: 5px 0 15px;
    display: inherit;
  }
}
#contact {
  font-weight: 700;
  line-height: 39px;
  font-size: 18.5px;
}

#contact a {
  text-decoration: none;
}

#contact a i {
  font-size: 22px;
  vertical-align: middle;
  display: inline;
}

@media (min-width: 992px) {
  #logo {
    float: left;
  }
  #contact {
    float: left;
    margin-left: 33px;
  }
  #search {
    margin-left: -35px;
  }
}
@media (min-width: 768px) and (max-width: 991px) {
  #logo {
    float: left;
  }
  #contact {
    float: right;
  }
}
/* search */
#search {
  margin-bottom: 10px;
}

#search .input-lg {
  height: 40px;
  line-height: 20px;
  padding: 0 10px;
}

#search .btn-lg {
  font-size: 15px;
  line-height: 18px;
  padding: 10px 20px;
  text-shadow: 0 1px 0 #FFF;
}

/* cart */
#cart {
  margin-bottom: 10px;
}

#cart > .btn {
  font-size: 12px;
  line-height: 18px;
  color: #FFF;
}

#cart.open > .btn {
  background-image: none;
  background-color: #FFFFFF;
  border: 1px solid #E6E6E6;
  color: #666;
  box-shadow: none;
  text-shadow: none;
}

#cart.open > .btn:hover {
  color: #444;
}

#cart .dropdown-menu {
  background: #eee;
  z-index: 1001;
}

#cart .dropdown-menu {
  min-width: 100%;
  overflow-y: auto;
  max-height: 800px;
  scrollbar-width: thin;
  overflow-x: hidden;
}

@media (max-width: 478px) {
  #cart .dropdown-menu {
    width: 100%;
  }
}
#cart .dropdown-menu table {
  margin-bottom: 10px;
}

#cart .dropdown-menu li > div {
  min-width: 427px;
  padding: 0 10px;
}

@media (max-width: 478px) {
  #cart .dropdown-menu li > div {
    min-width: 100%;
  }
}
#cart .dropdown-menu li p {
  margin: 20px 0;
}

/* menu -fix */
#menu {
  background-image: linear-gradient(to bottom, #4979cf, #2f64ca);
  background-repeat: repeat-x;
  min-height: 40px;
}

#menu .nav > li > a {
  color: #fff;
  text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.25);
  padding: 10px 15px 10px 15px;
  min-height: 15px;
  background-color: transparent;
}

#menu .nav > li > a:hover, #menu .nav > li.open > a {
  background-color: rgba(0, 0, 0, 0.1);
}

#menu .dropdown-menu {
  padding-bottom: 0;
}

#menu .dropdown-inner {
  display: table;
}

#menu .dropdown-inner ul {
  display: table-cell;
}

#menu .dropdown-inner a {
  min-width: 160px;
  display: block;
  padding: 3px 20px;
  clear: both;
  line-height: 20px;
  color: #333333;
  font-size: 12px;
}

#menu .dropdown-inner li a:hover {
  color: #FFFFFF;
}

#menu .see-all {
  display: block;
  margin-top: 0.5em;
  border-top: 1px solid #e4f1fc;
  padding: 3px 20px;
  -webkit-border-radius: 0 0 4px 4px;
  -moz-border-radius: 0 0 4px 4px;
  border-radius: 0 0 3px 3px;
  font-size: 12px;
}

#menu .see-all:hover, #menu .see-all:focus {
  text-decoration: none;
  color: #ffffff;
  background-color: #376aca;
  background-image: linear-gradient(to bottom, #4979cf, #2f64ca);
  background-repeat: repeat-x;
}

#menu #category {
  float: left;
  padding-left: 15px;
  font-size: 16px;
  font-weight: 700;
  line-height: 40px;
  color: #fff;
  text-shadow: 0 1px 0 rgba(0, 0, 0, 0.2);
}

#menu .btn-navbar {
  font-size: 15px;
  font-stretch: expanded;
  color: #FFF;
  padding: 2px 18px;
  float: right;
  background-color: #376aca;
  background-image: linear-gradient(to bottom, #4979cf, #2f64ca);
  background-repeat: repeat-x;
  border-color: #2f64ca #2f64ca #2e63ca;
}

#menu .btn-navbar:hover, #menu .btn-navbar:focus, #menu .btn-navbar:active, #menu .btn-navbar.disabled, #menu .btn-navbar[disabled] {
  color: #ffffff;
  background-color: #376aca;
}

@media (min-width: 768px) {
  #menu .dropdown:hover .dropdown-menu {
    display: block;
  }
}
@media (max-width: 767px) {
  #menu {
    border-radius: 4px;
  }
  #menu div.dropdown-inner > ul.list-unstyled {
    display: block;
  }
  #menu div.dropdown-menu {
    margin-left: 0 !important;
    padding-bottom: 10px;
    background-color: rgba(0, 0, 0, 0.1);
  }
  #menu .dropdown-inner {
    display: block;
  }
  #menu .dropdown-inner a {
    width: 100%;
    color: #fff;
  }
  #menu .dropdown-menu a:hover,
  #menu .dropdown-menu ul li a:hover {
    background: rgba(0, 0, 0, 0.1);
  }
  #menu .see-all {
    margin-top: 0;
    border: none;
    border-radius: 0;
    color: #fff;
  }
}
/* content */
#content {
  min-height: 600px;
}

/* footer */
footer {
  margin-top: 30px;
  padding-top: 30px;
  background-color: #e42e2c;
  border-top: 1px solid #e4f1fc;
  color: #e2e2e2;
}

footer .container * {
  color: #ffffff;
}

footer a {
  color: #dee7f9;
}

footer a:hover, footer a:focus {
  color: #fff;
}

footer h5 {
  font-family: "Open Sans", sans-serif;
  font-size: 13px;
  font-weight: bold;
  color: #bde1ff;
}

footer .list-unstyled {
  padding-left: 15px;
  list-style: square;
}

footer p {
  text-align: right;
}

/* alert */
.alert {
  padding: 8px 14px 8px 14px;
}

/* breadcrumb */
.breadcrumb {
  margin: 0 0 20px 0;
  padding: 0;
  border: 1px solid #e4f1fc;
  background-color: #f2f8fd;
}

html[dir=rtl] .breadcrumb > li::after {
  top: -3px;
  left: -5px;
  width: 26px;
  height: 26px;
  border-left: 1px solid #DDD;
  border-top: 1px solid #DDD;
  right: unset;
  border-right: unset;
  border-bottom: unset;
}

.pagination {
  margin: 0;
}

/* buttons */
.buttons {
  margin: 1em 0;
}

.btn {
  padding: 7.5px 12px;
  font-size: 12px;
  border: 1px solid #cccccc;
  border-radius: 4px;
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.2), 0 1px 2px rgba(0, 0, 0, 0.05);
}

.btn-xs {
  font-size: 9px;
}

.btn-sm {
  font-size: 10.2px;
}

.btn-lg {
  padding: 10px 16px;
  font-size: 15px;
}

.btn-group > .btn, .btn-group > .dropdown-menu, .btn-group > .popover {
  font-size: 12px;
}

.btn-group > .btn-xs {
  font-size: 9px;
}

.btn-group > .btn-sm {
  font-size: 10.2px;
}

.btn-group > .btn-lg {
  font-size: 15px;
}

.btn-default {
  color: #777;
  text-shadow: 0 1px 0 rgba(255, 255, 255, 0.5);
  background-color: #e7e7e7;
  background-image: linear-gradient(to bottom, #f2f8fd, #f1f8fd);
  background-repeat: repeat-x;
  border-color: #e7f2fc #e8f4fe #d1e8ff #daedfe;
}

.btn-default.active, .btn-default:active, .open > .dropdown-toggle.btn-default {
  box-shadow: inset 0 3px 11px rgba(91, 178, 255, 0.28);
  background-color: #f1f8fd;
  border-color: #bbd7f3;
}

.btn-default:hover {
  background-color: rgba(176, 213, 248, 0.2901960784);
  border-color: #c1dffa;
}

.btn-default.active.focus, .btn-default.active:focus, .btn-default.active:hover, .btn-default:active.focus,
.btn-default:active:focus, .btn-default:active:hover, .open > .dropdown-toggle.btn-default.focus,
.open > .dropdown-toggle.btn-default:focus, .open > .dropdown-toggle.btn-default:hover {
  background-color: rgba(176, 213, 248, 0.2901960784);
  border-color: #c1dffa;
}

.btn-primary {
  color: #ffffff;
  text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.25);
  background-color: #376aca;
  background-image: linear-gradient(to bottom, #4979cf, #2f64ca);
  background-repeat: repeat-x;
  border-color: #2f64ca #2f64ca #2e63ca;
}

.btn-primary:hover, .btn-primary:active, .btn-primary.active, .btn-primary.disabled, .btn-primary[disabled] {
  background-color: #2f64ca;
  background-position: 0 -15px;
}

.btn-warning {
  color: #ffffff;
  text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.25);
  background-color: #faa732;
  background-image: linear-gradient(to bottom, #fbb450, #f89406);
  background-repeat: repeat-x;
  border-color: #f89406 #f89406 #ad6704;
}

.btn-warning:hover, .btn-warning:active, .btn-warning.active, .btn-warning.disabled, .btn-warning[disabled] {
  box-shadow: inset 0 1000px 0 rgba(0, 0, 0, 0.1);
}

.btn-danger {
  color: #ffffff;
  text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.25);
  background-color: #da4f49;
  background-image: linear-gradient(to bottom, #ee5f5b, #bd362f);
  background-repeat: repeat-x;
  border-color: #bd362f #bd362f #802420;
}

.btn-danger:hover, .btn-danger:active, .btn-danger.active, .btn-danger.disabled, .btn-danger[disabled] {
  box-shadow: inset 0 1000px 0 rgba(0, 0, 0, 0.1);
}

.btn-success {
  color: #ffffff;
  text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.25);
  background-color: #5bb75b;
  background-image: linear-gradient(to bottom, #62c462, #51a351);
  background-repeat: repeat-x;
  border-color: #51a351 #51a351 #387038;
}

.btn-success:hover, .btn-success:active, .btn-success.active, .btn-success.disabled, .btn-success[disabled] {
  box-shadow: inset 0 1000px 0 rgba(0, 0, 0, 0.1);
}

.btn-info {
  color: #ffffff;
  text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.25);
  background-color: #df5c39;
  background-image: linear-gradient(to bottom, #e06342, #dc512c);
  background-repeat: repeat-x;
  border-color: #dc512c #dc512c #a2371a;
}

.btn-info:hover, .btn-info:active, .btn-info.active, .btn-info.disabled, .btn-info[disabled] {
  background-image: none;
  background-color: #df5c39;
}

.btn-link {
  border-color: rgba(0, 0, 0, 0);
  cursor: pointer;
  color: #4979cf;
  border-radius: 0;
}

.btn-link, .btn-link:active, .btn-link[disabled] {
  background-color: rgba(0, 0, 0, 0);
  background-image: none;
  box-shadow: none;
}

.btn-inverse {
  color: #ffffff;
  text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.25);
  background-color: #444;
  border: none;
  border-radius: 5px;
}

.btn-inverse:hover, .btn-inverse:active, .btn-inverse.active, .btn-inverse.disabled, .btn-inverse[disabled] {
  background-color: #222222;
  background-image: linear-gradient(to bottom, #333333, #111111);
}

/* list group */
.list-group a {
  border: 1px solid #e4f1fc;
  color: #888888;
  padding: 8px 12px;
}

.list-group a.active, .list-group a.active:hover, .list-group a:hover {
  color: #444444;
  background: #f2f8fd;
  border: 1px solid #e4f1fc;
  text-shadow: 0 1px 0 #FFF;
}

/* carousel */
#common-home .slideshow.swiper-viewport,
#content .swiper-viewport {
  border: 1px solid #f3f8fd;
  box-shadow: 0 1px 4px rgb(236, 244, 252);
}

.carousel-caption {
  color: #FFFFFF;
  text-shadow: 0 1px 0 #000000;
}

.carousel img {
  display: initial;
}

.carousel-control .icon-prev:before {
  content: "\f053";
  font-family: FontAwesome;
}

.carousel-control .icon-next:before {
  content: "\f054";
  font-family: FontAwesome;
}

.swiper-viewport .swiper-button-next::before, .swiper-viewport .swiper-button-prev::before {
  color: rgb(55, 106, 203);
}

.swiper-pagination .swiper-pagination-bullet {
  background-color: rgb(234, 244, 252);
  box-shadow: inset 0 0 3px rgba(12, 93, 246, 0.3);
}

.swiper-pagination .swiper-pagination-bullet-active {
  background-color: rgb(62, 112, 204);
}

/* product list */
.product-thumb {
  border: 1px solid #f3f8fd;
  box-shadow: 0 1px 4px rgb(252, 236, 241);
  margin-bottom: 20px;
  overflow: auto;
}

.product-thumb:hover {
  border-color: #FF6569;
  box-shadow: 0 1px 6px rgb(255, 209, 209);
}

.product-thumb .image {
  text-align: center;
}

.product-thumb .image a {
  display: block;
}

.product-thumb .image img {
  margin-left: auto;
  margin-right: auto;
}

.product-grid .product-thumb .image {
  float: none;
}

.thumbnail, .img-thumbnail {
  border: 1px solid #e42;
}

@media (min-width: 767px) {
  .product-list .product-thumb .image {
    float: left;
    padding: 0 16.5px;
  }
}
.product-thumb h4 {
  font-weight: bold;
}
.product-thumb h4 a {
  color: #444;
}

.product-thumb .caption {
  padding: 0 20px;
  min-height: 174px;
}
.product-thumb .caption p {
  color: #434343;
}

.product-list .product-thumb .caption {
  margin-left: 230px;
}

@media (max-width: 1200px) {
  .product-grid .product-thumb .caption {
    min-height: 210px;
    padding: 0 10px;
  }
}
@media (max-width: 767px) {
  .product-list .product-thumb .caption {
    min-height: 0;
    margin-left: 0;
    padding: 0 10px;
  }
  .product-grid .product-thumb .caption {
    min-height: 0;
  }
}
.product-thumb .rating {
  padding-bottom: 5px;
}

.rating .fa-stack {
  font-size: 8px;
}

.rating .fa-star-o {
  color: #999;
  font-size: 15px;
}

.rating .fa-star {
  color: #FC0;
  font-size: 15px;
}

.rating .fa-star + .fa-star-o {
  color: #E69500;
}

h2.price {
  margin: 0;
}

.product-thumb .price {
  color: gold;
  font-size: 16px;
}

.product-thumb .price-new {
  font-weight: 600;
}

.product-thumb .price-old {
  color: #999;
  text-decoration: line-through;
  margin-left: 10px;
}

.product-thumb .price-tax {
  color: #999;
  font-size: 12px;
  display: block;
}

.product-thumb .button-group {
  border-top: 1px solid #E7EAEA;
  overflow: auto;
}

.product-list .product-thumb .button-group {
  border-left: 1px solid #e4f1fc;
}

@media (max-width: 768px) {
  .product-list .product-thumb .button-group {
    border-left: none;
  }
}
.product-thumb .button-group button {
  width: 60%;
  border: none;
  display: inline-block;
  float: left;
  background-color: #e42e2c;
  color: white;
  line-height: 38px;
  font-weight: bold;
  text-align: center;
  text-transform: uppercase;
}

.product-thumb .button-group button + button {
  width: 20%;
  border-left: 1px solid #e4f1fc;
}

.product-thumb .button-group button:hover {
  color: #343434;
  background-color: #E42;
  text-decoration: none;
  cursor: pointer;
}

@media (max-width: 1200px) {
  .product-thumb .button-group button, .product-thumb .button-group button + button {
    width: 33.33%;
  }
}
@media (max-width: 767px) {
